.blue-pod{.pod {
  box-shadow: inset 0 0 5em blue;
}}.red-pod{.pod {
  box-shadow: inset 0 0 5em red;
}}.gallery-peek{& {
  width: 100%;
  height: 25em;
  display: flex;
}

.peek {
  height: 100%;
  flex: 2;

  &.mars {
    background: no-repeat center/50% url('/assets/images/mars.jpg');
  }

  &.sneakers {
    background: no-repeat center/40% url('/assets/images/snickers.jpg');
  }
}

.close {
  flex: 1;
  width: 100px;
  height: 100px;
  background: no-repeat center/40% url('/assets/images/cancel-circle.svg');
}

}.gallery{& {
  display: flex;
  flex-wrap: wrap;
}

.pod {
  height: 200px;
  width: 200px;
  margin: 1em;
  border-radius: 50%;
  background: no-repeat center/80% url('/assets/images/pod.png');
}}